<template>
  <BlockEditorLayout
    class="media-html-editor media-html"
    :value="value"
    @input="$emit('input', $event)"
    @delete="$emit('delete')"
    @click-settings="$emit('click-settings')"
    v-bind="$attrs"
  >
    <template #toolbar>
      <select
        v-model="selectValue"
        class="ui-clickable"
      >
        <option value="p">P</option>
        <option value="h1">H1</option>
        <option value="h2">H2</option>
        <option value="h3">H3</option>
      </select>

      <button
        v-for="(option, i) in formatButtons"
        :key="i"
        type="button"
        @click="option.callback"
        class="ui-clickable"
        :class="{'--active': option.isActive}"
      >{{ option.text }}</button>
    </template>

    <template #default>
      <EditorContent
        class="tiptap-editor-contents theme-html"
        :editor="editor"
      />
    </template>

  </BlockEditorLayout>
</template>

<script>
import { Editor, EditorContent } from 'tiptap';
import {
  Bold,
  Code,
  Italic,
  Link,
  Underline,
  Heading,
  History,
  Placeholder,
  HardBreak,
  // Image,
} from 'tiptap-extensions';
import Image from './tiptap/Img';

import BlockEditorLayout from '../../../../components/CmsBlockEditor/EditorLayout.vue';
export default {
  name: 'MediaHtmlEditor',

  components: { BlockEditorLayout, EditorContent },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: '',

      // Create an `Editor` instance with some default content. The editor is
      // then passed to the `EditorContent` component as a `prop`
      editor: new Editor({
        extensions: [
          new Bold(),
          new Code(),
          new Italic(),
          new Link(),
          new Underline(),
          new Heading({ levels: [1, 2, 3] }),
          new HardBreak(),
          new History(),
          new Image(),

          new Placeholder({
            emptyEditorClass: 'is-editor-empty',
            emptyNodeClass: 'is-empty',
            emptyNodeText: 'Escribe aqui ...',
            showOnlyWhenEditable: true,
            showOnlyCurrent: true,
          }),
        ],

        content: '',

        onUpdate: ({ getHTML }) => {
          // get new content on update
          this.innerValue = getHTML();
          this.emitInput();
        },

        onFocus: () => {
          this.$emit('focus');
        },

        onBlur: () => {
          this.$emit('blur');
        },
      }),
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        let incomingValue =
          newValue && newValue.props && newValue.props.value
            ? newValue.props.value
            : '';

        if (incomingValue != this.innerValue) {
          this.innerValue = incomingValue;
          this.editor.setContent(this.innerValue);
        }
      },
    },
  },

  computed: {
    selectValue: {
      get() {
        let options = ['p', 'h1', 'h2', 'h3'];
        for (let i = 0; i < options.length; i++) {
          let optionName = options[i];
          if (
            this.allCommands[optionName] &&
            this.allCommands[optionName].isActive
          ) {
            return optionName;
          }
        }

        return 'p';
      },

      set(newValue) {
        if (!this.allCommands[newValue]) {
          return;
        }

        this.allCommands[newValue].callback();
      },
    },

    allCommands() {
      return {
        p: {
          text: 'P',
          callback: this.editor.commands.paragraph,
          isActive: this.editor.isActive.paragraph(),
        },
        h1: {
          text: 'H1',
          callback: () => this.editor.commands.heading({ level: 1 }),
          isActive: this.editor.isActive.heading({ level: 1 }),
        },
        h2: {
          text: 'H2',
          callback: () => this.editor.commands.heading({ level: 2 }),
          isActive: this.editor.isActive.heading({ level: 2 }),
        },
        h3: {
          text: 'H3',
          callback: () => this.editor.commands.heading({ level: 3 }),
          isActive: this.editor.isActive.heading({ level: 3 }),
        },
        bold: {
          text: 'B',
          callback: this.editor.commands.bold,
          isActive: this.editor.isActive.bold(),
        },
        italic: {
          text: 'I',
          callback: this.editor.commands.italic,
          isActive: this.editor.isActive.italic(),
        },
        underline: {
          text: 'U',
          callback: this.editor.commands.underline,
          isActive: this.editor.isActive.underline(),
        },
      };
    },

    formatButtons() {
      return [
        this.allCommands.bold,
        this.allCommands.italic,
        this.allCommands.underline,
      ];
    },
  },

  methods: {
    emitInput() {
      let clone = JSON.parse(JSON.stringify(this.value));
      clone.props = Object.assign({}, clone.props, { value: this.innerValue });
      this.$emit('input', clone);
    },
  },

  beforeDestroy() {
    this.editor.destroy();
  },
};
</script>

<style lang="scss">
// Fix placeholder not showing text (?)
.is-empty:first-child::before {
  content: attr(data-empty-text);
  float: left;
  color: lightgray;
  pointer-events: none;
  height: 0;
}

.tiptap-editor-contents [contenteditable] {
  min-height: 32px;
  outline: none;
}
</style>